const Yanychar = {}

Yanychar.makeNumber = function (day1, day2, day3, day4, day5, day6, day7) {
    const date = Number(localStorage.getItem('date'))
    const dateNow = new Date(2022, 6, 29)
    const dateCount = Math.round((dateNow - date) / 86400000)
    console.log(date, dateNow, dateCount)
    let res = null
    switch (dateCount) {
        case 1:
            res = day1
            break
        case 2:
            res = day2
            break
        case 3:
            res = day3
            break
        case 4:
            res = day4
            break
        case 5:
            res = day5
            break
        case 6:
            res = day6
            break
        case 7:
            res = day7
            break
        default:
            const date = Date.now()
            localStorage.setItem('date', date)
            window.location.reload()
            break
    }
    console.log(res)
    return res
}

export default Yanychar
